* {
  box-sizing: border-box; }

body {
  margin: 0;
  font-family: 'Source Code Pro';
  background-color: rgba(25, 84, 147, 0.8); }

.grid {
  display: flex;
  width: 100%;
  padding-top: 50px;
  justify-content: space-around; }

.editor-container {
  width: 30%; }
  .editor-container header {
    width: 100%;
    padding-top: 3px;
    padding-bottom: 3px;
    background-color: #54111d;
    border: 1px solid black;
    color: white;
    box-shadow: 3px 2px 8px 1px black;
    font-weight: bold;
    text-align: center; }

#editor {
  display: block;
  resize: vertical;
  padding: 20px 20px;
  width: 100%;
  border: 1px solid black;
  border-top: none;
  min-height: 100vh;
  box-shadow: 3px 2px 8px 1px black; }

#editor:focus {
  outline: none; }

.preview-container {
  width: 50%;
  margin-bottom: 20px; }
  .preview-container header {
    width: 100%;
    padding-top: 3px;
    padding-bottom: 3px;
    background-color: #54111d;
    border: 1px solid black;
    color: white;
    box-shadow: 3px 2px 8px 1px black;
    font-weight: bold;
    text-align: center; }

#preview {
  word-wrap: break-word;
  width: 100%;
  padding-top: 20px;
  padding-left: 20px;
  min-height: 700px;
  background-color: white;
  border: 1px solid black;
  border-top: none;
  box-shadow: 3px 2px 8px 1px black; }

.full-screen {
  width: 95%;
  margin: auto;
  margin-bottom: 20px; }

.hide {
  display: none; }

.panel {
  min-width: 30px;
  float: right;
  padding-right: 15px; }

.panel:hover {
  color: #c10022; }

